@import url(https://fonts.googleapis.com/css?family=Numans);
html{
  height:100%;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  height:100%;
}

#root{
  height:100%;
}
.App {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding:50px;
}



.login-wrapper{
    padding-top: 10rem;
    padding-bottom: calc(10rem - 72px);
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(92, 77, 66, 0.8)), to(rgba(92, 77, 66, 0.8))), url(/static/media/bg-masthead.bde300d6.jpg);
    background: linear-gradient(to bottom, rgba(92, 77, 66, 0.8) 0%, rgba(92, 77, 66, 0.8) 100%), url(/static/media/bg-masthead.bde300d6.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-size: cover;
    height: 100vh;
    font-family: 'Numans', sans-serif;
}

.login-wrapper .card-container.card {
    max-width: 350px;
    padding: 40px 40px;
}

.login-wrapper .btn {
    font-weight: 700;
    height: 36px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    cursor: default;
}

/*
 * Card component
 */
.login-wrapper .card {
    background-color: #F7F7F7;
    /* just in case there no content*/
    padding: 20px 25px 30px;
    margin: 0 auto 25px;
    margin-top: 50px;
    /* shadows and rounded borders */
    border-radius: 2px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.login-wrapper .profile-img-card {
    width: 96px;
    height: 96px;
    margin: 0 auto 10px;
    display: block;
    border-radius: 50%;
}

/*
 * Form styles
 */
.login-wrapper .profile-name-card {
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    margin: 10px 0 0;
    min-height: 1em;
}

.login-wrapper .reauth-email {
    display: block;
    color: #404040;
    line-height: 2;
    margin-bottom: 10px;
    font-size: 14px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    box-sizing: border-box;
}

.login-wrapper .form-signin #inputEmail,
.login-wrapper .form-signin #inputPassword {
    direction: ltr;
    height: 44px;
    font-size: 16px;
}

.login-wrapper .form-signin input[type=email],
.login-wrapper .form-signin input[type=password],
.login-wrapper .form-signin input[type=text],
.login-wrapper .form-signin button {
    width: 100%;
    display: block;
    margin-bottom: 10px;
    z-index: 1;
    position: relative;
    box-sizing: border-box;
}

.login-wrapper .form-signin .form-control:focus {
    border-color: rgb(104, 145, 162);
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgb(104, 145, 162);
}

.login-wrapper .btn.btn-signin {
    /*background-color: #4d90fe; */
    background-color: rgb(104, 145, 162);
    /* background-color: linear-gradient(rgb(104, 145, 162), rgb(12, 97, 33));*/
    padding: 0px;
    font-weight: 700;
    font-size: 14px;
    height: 36px;
    border-radius: 3px;
    border: none;
    -webkit-transition: all 0.218s;
    transition: all 0.218s;
}

.login-wrapper .btn.btn-signin:hover,
.login-wrapper .btn.btn-signin:active,
.login-wrapper .btn.btn-signin:focus {
    background-color: rgb(12, 97, 33);
}

.login-wrapper .forgot-password {
    color: rgb(104, 145, 162);
}

.login-wrapper .forgot-password:hover,
.login-wrapper .forgot-password:active,
.login-wrapper .forgot-password:focus{
    color: rgb(12, 97, 33);
}

.login-error{
    margin-top: 5px;
    padding: 15px;
    background-color: #ebccd1;
    color: #a94442;
    font-size: 12px;
}
.chat-room{
    height:100%;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 800px;
    border-radius: 10px;
    overflow: hidden;
}

.chat-room .chat-history{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: scroll;
    overflow-x: hidden;
}

.chat-room .message-wrapper{
    width: 100%;
    display: flex;
    flex-direction: row;
    min-height: 100px;
    border-top: 1px solid black;
}

.chat-room .message-wrapper textarea{
    resize: none;
    flex: 1 1;
    border: none;
    border-right: 1px solid black;
}

.chat-room .btn{
    border-radius: 0;
    border: none;
}

.chat-room .message{
    padding: 20px 20px;
    max-width: 40%;
}

.chat-room .self-message{
    align-self: flex-end;
}

.chat-room .message .message-title{
    display: flex;
    flex-direction: row;
    padding-left: 5px;
}

.chat-room .message .message-title .username {
    padding-right: 10px;
}

.chat-room .message .message-bubble{
    margin: 10px 0;
    padding: 10px 20px;
    border-radius: 10px;
    background: #007bff;
    color: white;
    white-space: pre-wrap;      /* CSS3 */
    white-space: -moz-pre-wrap; /* Firefox */
    white-space: -o-pre-wrap;   /* Opera 7 */
    word-wrap: break-word;      /* IE */
}
